<template>
  <div class="preload">
    <div class="limit_screen">
      <img class="preload_logo" src="../assets/logo.png" alt="">
      <img class="preload_score" src="../assets/nombrescore2.png" alt="">
      <div class="preload_loader">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  created() {
    setTimeout(()=>{
      this.$router.push('/bienvenido');
    }, 1000);
  }
}
</script>

<style scoped>
  .preload{
    width: 100%;
    height: 100vh;
    display: flex;
    background-image: url('../assets/background2.jpg');
    background-position: 75%;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .limit_screen{
    margin: 0 auto;
    max-width: 550px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .preload_logo{
    width: 60%;
    margin-bottom: 30px;
  }
  .preload_score{
    width: 300px;
    margin-bottom: 150px;
  }
  .preload_loader{
    width: 300px;
    height: 60px;
    background-image: url('../assets/cargando25.png');
    background-size: 100% 100%;
    animation: loader 1s infinite;
    margin-bottom: 150px;
  }
  @keyframes loader {
    25%  {background-image: url('../assets/cargando25.png');}
    50%  {background-image: url('../assets/cargando50.png');}
    100% {background-image: url('../assets/cargando100.png');}
}

</style>



