<template>
  <div class="login">
    <img class="login_logo" src="../assets/logo.png" alt="">
    <input type="text" class="login_cedula" value="">
    <button class="actionLogin" v-on:click="checkData"></button>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
    }
  },
  methods: {
    checkData() {
      this.$router.push('/cargando')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .login{
      width: 100%;
      height: 100vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-image: url('../assets/background1.jpg');
      background-position: 75%;
      background-size: cover;
      background-repeat: no-repeat;
  }
  .login_logo{
    width: 80%;
    margin-bottom: 100px;
  }
  .login_cedula{
    width: 300px;
    height: 63px;
    border-style: none;
    background-color: transparent;
    background-image: url('../assets/cedula.png');
    background-size: cover;
    outline: none;
    padding: 0px 20px;
    padding-top: 7px;
    color: #FFF;
    font-weight: 900;
    font-size: 35px;
    text-align: center;
    margin-bottom: 20px;
    font-family: 'Skranji', cursive;
    -webkit-text-stroke: 2px #724117;
  }
  .actionLogin{
    width: 150px;
    height: 50px;
    border-style: none;
    background-color: transparent;
    background-image: url('../assets/login.png');
    background-size: cover;
    outline: none;
    transition: .3s;
  }
  .actionLogin:hover{
    transform: scale(1.1);
  }
</style>



