<template>
  <div class="answers">
    <div class="limit_screen">
      <!-- <img class="answers_logo" src="../assets/logo.png" alt=""> -->
      <div class="answers_container">
        <img src="../assets/fondotextos.png" alt="">
        <div class="answers_container_content">
          <div class="answers_container_title">
            <img src="../assets/cinta.png" alt="">
            <h2>Preguntas</h2>
          </div>
          <div class="answers_container_list">
            <div class="answers__answer" v-for="n in 4"  :id="`answer${n}`" v-on:click.prevent="selectAnswer(n)">
              <img class="background" src="../assets/preguntaoff.png">
              <div class="content">
                <img class="answers__answer_number" :src="setImg(n)" alt="">
                Respuesta {{n}}
              </div>
            </div>
          </div>
          <router-link to="/resultado" class="answers_container_next"><img src="../assets/right.png" alt=""></router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        answers: [
          {
            id: 5,
            text: 'Pregunta 1'
          },{
            id: 6,
            text: 'Pregunta 2'
          },{
            id: 7,
            text: 'Pregunta 3'
          },{
            id: 8,
            text: 'Pregunta 4'
          }
        ],
      }
    },
    methods: {
      setImg(n){
        return require(`../assets/${n}.png`);
      },
      selectAnswer(n) {
        let img = require('../assets/respuestaseleccionada.png')
        let img2 = require('../assets/preguntaoff.png')
        for(let answer of document.querySelectorAll('.answers__answer .background')){
          answer.setAttribute("src", img2)
        }
        document.getElementById(`answer${n}`).childNodes[0].setAttribute("src", img)
      }
    }
  }
</script>
<style scoped>
  .answers{
    width: 100%;
    height: 100vh;
    display: flex;
    background-image: url('../assets/background3.jpg');
    background-position: 42%;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .limit_screen{
    max-width: 550px;
    width: 100%;
    height: 100vh;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }
  .answers_container{
    position: relative;
    margin: 7%;
  }
  .answers_container > img{
    max-width: 100%;
    max-height: 100vh;
  }
  .answers_container_content{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  .answers_container_title{
    margin-top: -1.4em;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .answers_container_title img{
    width: 80%;
  }
  .answers_container_title h2{
    position: absolute;
    top: 15%;
    color: #FFF;
    font-size: 1.2em;
  }
  .answers_container_list{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .answers__answer{
    position: relative;
    width: 80%;
  }
  .answers__answer .background{
    width: 100%;
  }
  .answers__answer .content{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 15px;
    color: #FFF;
    -webkit-text-stroke: 1.5px #453119;
    font-weight: bold;
    font-family: 'Skranji', cursive;
    font-size: 1.3em;
  }
  .answers__answer_number{
    height: 70%;
  }
  .answers_container_next{
    width: 60px;
  }
</style>



